*{
    box-sizing: border-box;
}

body{
    font-family: Montserrat, sans-serif;
    margin: 3rem;
    background-color: lavender;
    display: flex;
    align-items: center;
    justify-content: center;

}

h1{
    color: rgb(226, 161, 32);
}

h2{
    color:midnightblue;
}

.btn{
    font: bold;
    font-family: Montserrat, sans-serif;
    font-size: 15px;
    padding: 0.3rem, 0.3rem;
    border-radius: 40px;
    cursor: pointer;
    background-color:rgb(12, 4, 46);
    color:lavender;
    margin: 0.8rem;
    padding: 0.8em;
}

.btn:hover {
    background-color: #403f6d;
    border-color: #403f6d;
  }
  
  .btn--alt {
    background-color: transparent;
    color: #413781;
  }
  
  .btn--alt:hover {
    background-color: #503c94;
  }
  
.card{
  background-color:rgb(210, 210, 238);
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  padding: 1rem;
  width: 23rem;
  align-items: center;
  display:flex;
  justify-content: center;
  
}

img{
  width: 100px;
  height:100px;
  border-color:honeydew;
  border-radius: 50%;
  margin:0.5rem;
}

.logout{
    font-family: Montserrat, sans-serif;
    font-weight: bold;
    font-size: 11px;
    padding: 0.3rem, 0.3rem;
    border-radius: 40px;
    border-color: transparent;
    cursor: pointer;
    background-color:transparent;
    color:rgb(211, 151, 33);
    margin: 0.8rem;
    padding: 0.8em;
}

.logout:hover {
  color: #403f6d;
}

.logout--alt {
  color: #483069;
}

.logout--alt:hover {
  background-color: #503c94;
}
